import React from "react";
import "./App.css";

let starter = [
  {
    recipeTitle: "Cookies",
    ingredients: ["Eggs", "Flour", "Cookie mix"],
    steps: [
      "Mix eggs and flour in a bowl",
      "Add cookie mix",
      "Cook in oven for 12 minutes",
      "Serve and enjoy :)"
    ]
  }
];
localStorage.setItem("_bschade18_recipes", JSON.stringify(starter));

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      visibility: false,
      currentRecipe: "",
      recipes: JSON.parse(localStorage.getItem("_bschade18_recipes")),
      dialogMode: ""
    };
    this.toggleVisibility = this.toggleVisibility.bind(this);

    this.closeVisibility = this.closeVisibility.bind(this);
    this.setCurrent = this.setCurrent.bind(this);

    this.fillForm = this.fillForm.bind(this);
    this.handleDelete = this.handleDelete.bind(this);
    this.handleEdit = this.handleEdit.bind(this);
  }
  componentDidMount() {
    let recipes = localStorage.getItem("_bschade18_recipes");
    let recipesObj = JSON.parse(recipes);

    let recipe = recipesObj[0].recipeTitle;

    this.setState({
      currentRecipe: recipe
    });
  }
  fillForm() {
    let currentRecipe = this.state.currentRecipe;
    let recipes = this.state.recipes;
    let recipe;
    for (var i = 0; i < recipes.length; i++) {
      if (currentRecipe == recipes[i].recipeTitle) {
        recipe = recipes[i];
      }
    }
    document.getElementById("edit-recipe").value = recipe.recipeTitle;
    document.getElementById("edit-ingredients").value = recipe.ingredients;
    document.getElementById("edit-steps").value = recipe.steps;
  }

  handleEdit(event) {
    let recipes = this.state.recipes;
    let currentRecipe = this.state.currentRecipe;
    let edit = recipes.filter(recipe => {
      return currentRecipe !== recipe.recipeTitle;
    });
    let updatedList = edit.concat({
      recipeTitle: document.getElementById("edit-recipe").value.split("/"),
      ingredients: document.getElementById("edit-ingredients").value.split("/"),
      steps: document.getElementById("edit-steps").value.split("/")
    });

    setTimeout(() => {
      localStorage.setItem("_bschade18_recipes", JSON.stringify(updatedList));
      this.setState({
        recipes: updatedList,
        visibility: false
      });
      this.toggleVisibility();
    }, 50);
  }
  toggleVisibility(e) {
    let recipeArray = this.state.recipes;
    let recipeList = [];
    for (var i = 0; i < recipeArray.length; i++) {
      recipeList.push(recipeArray[i].recipeTitle.toLowerCase());
    }
    if (e.target.id == "edit") {
      this.setState({
        dialogMode: "edit"
      });
      setTimeout(() => this.fillForm(), 20);
    } else {
      this.setState({
        dialogMode: "add"
      });
    }
    if (this.state.visibility) {
      if (
        recipeList.includes(
          document.getElementById("edit-recipe").value.toLowerCase()
        )
      ) {
        alert("That recipe already exists");
        return;
      }
      let updatedRecipes = recipeArray.concat({
        recipeTitle: document.getElementById("edit-recipe").value,
        ingredients: document
          .getElementById("edit-ingredients")
          .value.split("/"),
        steps: document.getElementById("edit-steps").value.split("/")
      });
      localStorage.setItem(
        "_bschade18_recipes",
        JSON.stringify(updatedRecipes)
      );
      this.setState({
        visibility: false,
        recipes: updatedRecipes,
        currentRecipe: document.getElementById("edit-recipe").value,
        recipeInput: "",
        ingredInput: "",
        stepsInput: ""
      });
    } else {
      this.setState({
        visibility: true
      });
    }
  }
  closeVisibility() {
    if (this.state.visibility) {
      this.setState({
        visibility: false
      });
    } else {
      this.setState({
        visibility: true
      });
    }
  }

  setCurrent(e) {
    this.setState({
      currentRecipe: e.target.innerHTML
    });
  }

  handleDelete() {
    let recipes = this.state.recipes;
    let currentRecipe = this.state.currentRecipe;
    let newIndex;

    for (var i = 0; i < recipes.length; i++) {
      if (recipes[i].recipeTitle == currentRecipe) {
        newIndex = i - 1;
      }
    }
    let confirmDelete = window.confirm(
      "Are you sure you want to delete this recipe?"
    );

    let filter = recipes.filter(recipe => {
      return recipe.recipeTitle !== currentRecipe;
    });

    if (confirmDelete == true) {
      localStorage.setItem("_bschade18_recipes", JSON.stringify(filter));
      setTimeout(() => {
        this.setState({
          recipes: filter,
          currentRecipe: recipes[newIndex].recipeTitle
        });
      }, 20);
    }
  }

  render() {
    return (
      <div>
        <h1 id="title">My Recipes</h1>
        <RecipeList recipes={this.state.recipes} setCurrent={this.setCurrent} />
        <RecipeDisplay
          toggleVisibility={this.toggleVisibility}
          currentRecipe={this.state.currentRecipe}
          recipes={this.state.recipes}
          handleDelete={this.handleDelete}
        />
        <RecipeForm
          toggleVisibility={this.toggleVisibility}
          closeVisibility={this.closeVisibility}
          visibility={this.state.visibility}
          dialogMode={this.state.dialogMode}
          currentRecipe={this.state.currentRecipe}
          recipes={this.state.recipes}
          editRecipe={this.handleEdit}
        />
      </div>
    );
  }
}

class RecipeList extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    let list = this.props.recipes.map((recipe, index) => {
      return (
        <div id="recipe-title" key={index} onClick={this.props.setCurrent}>
          {recipe.recipeTitle}
        </div>
      );
    });

    return <div id="recipe-list">{list}</div>;
  }
}

class RecipeDisplay extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    let showRecipe;
    let recipes = this.props.recipes;
    let currentRecipe = this.props.currentRecipe;
    let cool = recipes[0].steps;
    for (var i = 0; i < recipes.length; i++) {
      if (currentRecipe == recipes[i].recipeTitle) {
        showRecipe = (
          <div className="recipeBox">
            <div className="recipeDisplay">
              <div id="show-recipe-title" className="recipe-display-pane">
                {currentRecipe}
              </div>
              <div className="recipe-display-pane">
                <i
                  id="edit"
                  className=""
                  onClick={this.props.toggleVisibility}
                  className="fas fa-edit"
                ></i>

                <i
                  onClick={this.props.handleDelete}
                  className="fas fa-trash"
                ></i>
              </div>
            </div>
            <p className="ingredients-heading">Ingredients:</p>
            <ul className="show-ingredients">
              {recipes[i].ingredients.map((ingredient, j) => (
                <li className="list-ingredients" key={j}>
                  {ingredient}
                </li>
              ))}
            </ul>
            <p className="directions-heading">Directions:</p>
            <ol>
              {recipes[i].steps.map((step, k) => (
                <li className="list-directions" key={k}>
                  {step}
                </li>
              ))}
            </ol>
            <i
              onClick={this.props.toggleVisibility}
              className="fas fa-plus"
            ></i>
          </div>
        );
      }
    }

    return <div>{showRecipe}</div>;
  }
}

class RecipeForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    if (this.props.visibility) {
      return (
        <div id="dialog">
          <div>
            {this.props.dialogMode == "edit" ? (
              <div>
                <div className="input-title">
                  Edit Recipe
                  <br />
                </div>
                <textarea
                  id="edit-recipe"
                  placeholder="Recipe Name"
                  type="text"
                />
                <br />
                <div className="input-title">
                  Ingredients
                  <br />
                </div>
                <textarea
                  id="edit-ingredients"
                  rows="5"
                  placeholder='Separate each ingredient with a "/"'
                  type="text"
                />
                <br />
                <div className="input-title">
                  Steps
                  <br />
                </div>
                <textarea
                  id="edit-steps"
                  rows="10"
                  placeholder='Seperate each direction with a "/" '
                  type="text"
                />
                <br />
              </div>
            ) : (
              <div>
                <div className="input-title">
                  Add Recipe
                  <br />
                </div>
                <textarea
                  id="edit-recipe"
                  placeholder="Recipe Name"
                  type="text"
                />
                <br />
                <div className="input-title">
                  Ingredients
                  <br />
                </div>
                <textarea
                  id="edit-ingredients"
                  rows="5"
                  placeholder='Separate each ingredient with a "/"'
                  type="text"
                />
                <br />
                <div className="input-title">
                  Steps
                  <br />
                </div>
                <textarea
                  id="edit-steps"
                  rows="10"
                  placeholder='Seperate each direction with a "/"'
                  type="text"
                />
                <br />
              </div>
            )}

            {this.props.dialogMode == "edit" ? (
              <button onClick={this.props.editRecipe}>Save</button>
            ) : (
              <button onClick={this.props.toggleVisibility}>Add</button>
            )}

            <button onClick={this.props.closeVisibility}>Close</button>
          </div>
        </div>
      );
    } else {
      return <div></div>;
    }
  }
}

export default App;
